import React, { Component } from 'react';
import './css/main.scss';

class SkillBlock extends Component {
  render(){
    return (
      <div id = 'skills'>
        <p>
          <img className = 'logo' src={require('./assets/coding.png')} />
          <div className = 'title'>What can I code for you ?</div>
          <hr />
        </p>
        <p>
          <ul id="categories">
            <li>Softwares</li>
            <p>
              <span className = "tag fav">C++</span>
              <span className = "tag blue">C#</span>
              <span className = "tag green">Qt</span>
              <span className = "tag dark-blue">VB</span>
            </p>
            <li>Websites</li>
            <p>
              <span className = "tag fav">PHP</span>
              <span className = "tag blue">SQL</span>
              <span className = "tag green">HTML</span>
              <span className = "tag dark-blue">CSS3</span>
              <span className = "tag red">SASS</span>
              <span className = "tag blue">JQuery</span>
              <br/><br/>
              <span className = "tag fav">React JS</span>
              <span className = "tag blue">Wordpress</span>
              <span className = "tag green">Cake PHP</span>
              <br/><br/>
              <span className = "tag fav">Git</span>
              <span className = "tag blue">GitHub</span>
              <span className = "tag green">Firebase</span>
              <span className = "tag dark-blue">Trello</span>
            </p>
            <li>Mobile Apps</li>
            <p>
              <span className = "tag fav">Java</span>
              <span className = "tag blue">Android st.</span>
            </p>
          </ul>
          <p>
            <img className = 'logo' src={require('./assets/speaker.png')} />
            <div className = 'title'>You can speak to me in</div>
            <hr />
          </p>
          <ul id = 'languages'>
            <li>Fluently</li>
            <p>
              <span className = "tag fav">French</span>
              <span className = "tag blue">English</span>
              <span className = "tag blue wide">Japanese(日本語)</span>
            </p>
            <li>Scolar level</li>
            <p><span className = "tag blue">Español</span></p>
          </ul>
        </p>
      </div>
    )
  }
}

export default SkillBlock;
